body{
  font-family: 'Open Sans', sans-serif !important;
  background: #1a1b1f;
  color: #fff;
}
.text-slanted{
  font-family: 'Courgette', cursive;
}

.img-link-container{
  overflow: hidden;
}
.img-link-img{
  transition: all 0.35s ease;
}
.img-link-container:hover .img-link-img{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  opacity: 0.5;
}
button:hover, input:hover, button:focus, input:focus{
  outline: none
}
a{
  color: inherit;
}
svg{
  fill: #900830;
  opacity: 0.5;
}
.navbar{
  background: #17161b;
 
}
.nav-item{
  color:#6b6e75;
}
.active, .navbar-brand{
  color: #900830;
  opacity: 0.8;
}
.hero-box{
  min-height: 100vh;
}
.navbar-brand:hover, svg:hover{
  opacity: 1;
  color: #900830;
}
.inpt-search{
  background: #232428;
  border: none;
  color: #900830;
  width: 35rem;
  padding: 0.3rem 0 0.3rem 3rem;
  border-radius: 1rem;
}
@media (max-width: 990px){
  .inpt-search{
    
    width: 100%;
    
  }
}
.search-container{
  position: relative;
}
.search-container span{
  position: absolute;
  left: 0.8rem;
  top: 0.3rem;
  color: #6b6e75;
}
.search-results{
  min-height: 100vh;
  position: relative;
  padding: 3rem 0;
}

.btn-black{
  color:#6b6e75;
  font-size: 1.2rem;
  border: 1px solid #6b6e75;
  border-radius: 1rem;
}
.btn-active{
  color: #900830;
  border: 1px solid #900830;
}

@media screen and (min-width:800px){
  .search-results{
  background:linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url('/MovieSearch/images/cinema3.jpg')center/cover fixed no-repeat;

  }
}
header{
  min-height: 100vh;
  background:linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)), url('/MovieSearch/images/cinema4.jpg')center/cover fixed no-repeat;
  position: relative;
  padding: 5rem 0;
}
.header-details{
  min-height: 100vh;
}
.img_mdb{
  opacity: 0.5;
  width: 15rem;
  margin-top: 3rem;
}
.movie-hero{
  min-height: 100vh;
  /* background: url('/images/cinema.jpg'); */
  position: relative;
}
.movie-detalis{
  background: rgba(23, 23, 23, 0.8);
}
.actor-hero{
  min-height: 100vh;
  background:linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url('/MovieSearch/images/cinema2.jpg')center/cover fixed no-repeat;
  position: relative;
  padding: 5rem 0;
}


